@font-face {
  font-family: "Wolkerskratchoutline";
  font-style: "normal";
  font-weight: 500;
  src: url("./utils/font/Wolkerskratchoutline.ttf");
}

@font-face {
  font-family: "Wolkerjuiceoutline";
  font-style: "normal";
  font-weight: 500;
  src: url("./utils/font/Wolkerjuiceoutline.ttf");
}
@font-face {
  font-family: "Wolkerjuiced";
  font-style: "normal";
  font-weight: 500;
  src: url("./utils/font/Wolkerjuiced.ttf");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--background);
  color: var(--text);
}

:root {
  --background: #000;
  --text: #fff;
}
